@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.home_section {
    position: relative;
    /* background-color: rgba(112, 93, 80, .8); */
    background-image: url(../img/glasses-lie-laptop-reflecting-light-from-screen-dark.jpg);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    opacity: .9;
    

}

.home__content {
    grid-template-columns: 116px repeat(1 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}


.home__title {
    font-size: 3.75rem;
    font-weight: 500;
    margin-bottom: 0.25rem;
    color: azure;
    padding-top: 17rem;
    padding-left: 15rem;
}


.home__subtitle{
    font-family: "Orbitron", sans-serif;
    position: relative;
    font-size: 1.85rem;
    padding-left: 5.4rem;
    /* padding-top: 0.5rem; */
    /* font-weight: 400; */
    margin-bottom: 1rem;
    color: azure;
    margin-left: 15.2rem;
    
    
}

.home__subtitle::before {
    content: '';
    position:absolute;
    width: 70px;
    height: 1px;
    background-color: #757575;
    left: 0;
    top: 1rem;
}


.home__description {
    font-size:large;
    font-weight: bold;
    max-width: 500px;
    margin-bottom: 3rem;
    color: azure;
    margin-left: 15.2rem;

}


  


.home__scroll{
    margin-left: 43.25rem;
    
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);

    }
}

.home__scroll-name {
    color: azure;
    font-weight: 400;
    margin-right: 0.25rem;
    margin-left: 2px;
    cursor: pointer;
   
}

.home__scroll-arrow {
    font-size: 1.25rem;
    color: azure;
}


a {
    text-decoration: none;
    
}

/* ---------- 반응형 ---------- */
@media (max-width: 1024px) {
  .home__title {
    font-size: 2.5rem;
    padding-left: 5rem;
    padding-top: 10rem;
  }
  .home__subtitle {
    font-size: 1.5rem;
    margin-left: 5rem;
    padding-left: 2rem;
  }
  .home__description {
    margin-left: 5rem;
  }
  .home__scroll {
    margin-left: 5rem;
  }
}

@media (max-width: 768px) {
  .home__title {
    font-size: 2rem;
    padding-left: 1rem;
    padding-top: 7rem;
  }
  .home__subtitle {
    font-size: 1.2rem;
    margin-left: 1rem;
    padding-left: 2rem;
  }
  .home__description {
    font-size: 1rem;
    margin-left: 1rem;
    max-width: 90%;
  }
  .home__scroll {
    margin-left: 0;
    text-align: center;
  }
}