/* 기본 nav 스타일 (PC 포함) */
nav {
  justify-content: space-between;
  align-items: center;
  height: 9vh;
  display: flex;
  padding-left: 3rem;
  padding-right: 3rem;
}

.nav-links li {
  cursor: pointer;
}

.NavContainer,
.transNavContainer {
  position: fixed;
  background-color: transparent;
  transition: 0.3s;
  width: 100%;
  z-index: 10;
}

/* 흰색 배경 구간 */
.NavContainer.white .nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;
  font-size: 1rem;
}

.NavContainer.white a {
  color: white;
  text-decoration: none;
}

.NavContainer.white a:hover {
  color: grey;
  text-decoration: underline;
  text-underline-offset: 1rem;
  text-decoration-color: rgb(181, 181, 181);
}

/* 검정 배경 구간 */
.transNavContainer.black .nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;
  font-size: 1rem;
}

.transNavContainer.black a {
  color: black;
  text-decoration: none;
}

.transNavContainer.black a:hover {
  color: grey;
  text-decoration: underline;
  text-underline-offset: 1rem;
  text-decoration-color: rgb(181, 181, 181);
}

.logo {
  font-size: 1.5rem;
}

.logo:hover {
  cursor: default;
}

.logo a {
  cursor: pointer;
}

nav a, .logo {
  user-select: none; /* 텍스트 선택 방지 */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}



/* --- 모바일 대응 --- */

.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  gap: 5px;
  z-index: 15;
}

.hamburger span {
  height: 3px;
  width: 25px;
  background-color: black;
  border-radius: 2px;
  transition: background-color 0.3s ease;
}

/* 흰색일때 햄버거 색도 흰색 */
.NavContainer.white .hamburger span,
.hamburger.white span {
  background-color: white;
}

/* 검정일때 햄버거 색 */
.transNavContainer.black .hamburger span,
.hamburger.black span {
  background-color: black;
}

/* 햄버거 열렸을 때 색 변경 */
.hamburger.active.white span {
  background-color: #ddd;
}

.hamburger.active.black span {
  background-color: #555;
}

/* 모바일에서 햄버거 보이기, 네비 숨기기 */
@media screen and (max-width: 768px) {
  nav {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  .hamburger {
    display: flex;
    position: fixed;
    top: 1.25rem;
    right: 1rem;
  }

  .nav-links {
    position: fixed;
    top: 9vh;
    right: 0;
    height: auto;
    background-color: rgba(255, 255, 255, 0.85); /* 투명도 조절 */
    flex-direction: column;
    gap: 1.5rem;
    list-style: none;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    margin: 0;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    border-radius: 0 0 0 10px;
    z-index: 14;
    min-width: 150px;
    width: auto; /* 글자 길이에 맞게 */
  }

  /* 메뉴 열렸을 때 */
  .nav-links.mobile-active {
    transform: translateX(0);
  }

  .nav-links li {
    padding: 0 1rem;
  }

  /* 메뉴 열렸을 때 글자 무조건 검정 */
  .nav-links.mobile-active a {
    color: black !important;
  }

  /* 메뉴 기본 글자색 */
  .nav-links a {
    color: black;
    font-weight: 600;
    text-decoration: none;
  }

  /* 흰색일 때 메뉴 링크 색상 (메뉴 닫혔을 때) */
  .NavContainer.white .nav-links a {
    color: white;
  }

  .nav-links a:hover {
    color: grey;
    text-decoration: underline;
    text-underline-offset: 1rem;
    text-decoration-color: rgb(181, 181, 181);
  }

  /* 로고 위치 */
  .logo {
    position: fixed;
    top: 1rem;
    left: 1rem;
    z-index: 20;
  }

  /* 모바일 메뉴 열렸을 때 body 스크롤 방지 */
  body.menu-open {
    overflow: hidden;
  }
}
