@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&family=Orbitron:wght@400..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.about_section {
    width: 100%;
    max-width: 71.25rem;
    padding: 4rem 1.5rem;
    margin: 0 auto;
}

.about__container {
    grid-template-columns: (2, 1fr);
    align-items: center;
    column-gap: 4rem;
}

.about__info {
    display: grid; /* 반드시 그리드 명시 */
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
    margin-bottom: 3.5rem;
    row-gap: 1.5rem;
    justify-content: center; /* PC도 가운데 정렬 가능 */
}

.about__icon {
    font-size: 2.5rem;
    color: #333333;
    margin-bottom: 0.5rem;
}

.about__box {
    display: flex;
    flex-wrap: nowrap;
    column-gap: 2rem;
    width: 100%;
    max-width: 14rem;
    margin: 0 auto;
    opacity: .8;
    align-items: center;
}

.about__title {
    font-family: "Noto Sans KR", sans-serif;
    font-size: 1rem; 
    font-weight: 700;
    margin-top: -5px;
    text-align: left;
    color:#212529;
}

.about__subtitle {
    font-size: 1.1rem;
    font-family: "Noto Sans KR", sans-serif;
    color: dimgray;
    font-weight: 500;
}

/* ---------- 반응형 ---------- */
@media (max-width: 1024px) {
  .about_section {
    padding: 4rem 3rem;
  }
  .about__title {
    font-size: 2rem;
  }
  .about__description {
    font-size: 1rem;
  }
}


@media (max-width: 768px) {
  .about_section {
    padding: 3rem 1rem;
  }
  
  .about__info {
    display: flex;
    flex-direction: column;
    align-items: center; /* 가로 가운데 정렬 */
    gap: 1.5rem;
    margin-bottom: 3.5rem;
  }
  
  .about__box {
    max-width: 100%;
    width: 100%;
    max-width: 320px; /* 적당한 최대 너비 지정 */
    padding: 0 1rem;
    flex-wrap: nowrap;
    justify-content: flex-start; /* 아이콘과 텍스트 좌측 정렬 */
  }
  
  .about__title {
    font-size: 1.3rem;
  }
  
  .about__subtitle {
    font-size: 1rem;
    word-break: break-word;
  }
}