/* GENERAL */

/* GOOGLE FONTS*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");


* {
    margin: 0;
    padding: 0;
}

body {
    font-family: "Poppins", sans-serif;
    overflow-x: hidden;
}

html {
    scroll-behavior: smooth;
    overflow-x: hidden;
}

p {
    color: rgb(85,85,85);
}

/* TRANSITION */

a,
.btn {
    transition: all 300ms ease;
    
}


.grid {
    display: grid;
    gap: 1.5rem;
  }

.section_a__title {
    font-size: 2.3rem;
    color:#333333;
    text-align: center;
    font-weight: 600;
}

.section_a__subtitle {
    display: block;
    font-size: 1rem;
    margin-bottom: 4rem;
    text-align: center;
}
