.Experience_section {
  background-color: #0f172a;
  padding: 80px 20px;
  color: white;
}

.section_a__title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 60px;
}

/* Timeline 전체 컨테이너 */
.timeline-container {
  position: relative;
  max-width: 900px;
  margin: 0 auto;
  padding: 0 20px;
}

/* 세로 라인 */
.timeline-container::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: #6366f1;
  transform: translateX(-50%);
}

/* 각 항목 카드 공통 */
.timeline-item {
  position: relative;
  width: 50%;
  padding: 20px 40px;
  box-sizing: border-box;
}

.timeline-item .dot {
  position: absolute;
  top: 24px;
  width: 16px;
  height: 16px;
  background-color: #6366f1;
  border-radius: 50%;
  border: 4px solid #0f172a;
  z-index: 1;
}

/* 왼쪽 카드 */
.timeline-item.left {
  left: 0;
  text-align: right;
}

.timeline-item.left .dot {
  right: -8px;
}

/* 오른쪽 카드 */
.timeline-item.right {
  left: 50%;
  text-align: left;
}

.timeline-item.right .dot {
  left: -8px;
}

/* 카드 내용 */
.experience-card-content {
  background-color: #1e293b;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.experience-card-content .title {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 4px;
}

.experience-card-content .company {
  font-size: 0.95rem;
  color: #d1d5db;
}

.experience-card-content .description {
  font-size: 0.85rem;
  color: #9ca3af;
  margin-top: 8px;
  line-height: 1.5;
}

/* 반응형 처리 */
@media (max-width: 768px) {
  .timeline-item {
    width: 100%;
    left: 0 !important;
    text-align: left !important;
    padding-left: 60px;
  }

  .timeline-item .dot {
    left: 20px !important;
    right: auto !important;
  }
}